body{
    margin: 0;
    background-color: rgb(228, 228, 228);
}

.preNav{
    background-color: rgb(29, 28, 28);
    display:grid;
    grid-template-columns: 5fr 2fr;
    color: white;
}

.preNav > div{
    padding: 0 4vmax;
    display: flex;
    align-items: center;
 }

 .preNav > div >span{
    color: rgba(180, 180, 180, 0.644);
    font-size: 0.8vmax;
    font-weight: 100;
}

.preNav > div svg{
    fill: rgb(180, 180, 180);
}

.preNav > div svg:hover{
    fill: rgb(255, 255, 255);
}


.preNav > div > a{

    color: rgb(180, 180, 180);
    font-size: 0.9vmax;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 300;
    margin: 1vmax;
    text-decoration: none;
}

.preNav > div > a:hover{
    color: white;
}
.nav{
    z-index: 1000;
    background-color: white;
    padding: 1vmax;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid rgba(43, 43, 43, 0.137);
}


.logo{
    background-color: orangered;
    width: 58px;
    border-radius: 20px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    right: 2vmax;
}

#logoImage{
    transition:all 0.2s;
    
    -webkit-transform: translateX(-58px);
    
            transform: translateX(-58px);
}

#logoImage:hover{
  
    -webkit-transform: translateX(0);
  
            transform: translateX(0);
}

.navlinks{
    color: rgb(59, 59, 59);
    text-decoration: none;
    margin: 1vmax 1.2vmax;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    font-size: 1.1vmax;
   
}

.navlinks:hover{
    color: orangered;
    text-decoration: none;
}

.searchbox{
    position: relative;
   left:5vmax;
}

.searchbox >input{
    border: none;
    border-bottom: 1px solid rgba(128, 128, 128, 0.76);
    width: 15vmax;
    font-size: 1.1vmax;
    outline: none;
    color:rgb(61, 61, 61);
    font-weight: 100;
    
  }
 
  
  .searchbox > svg{
    fill: rgb(61, 61, 61);
}
.offersSection{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1vmax;
  }

  .offersSection   img{
    margin: 0 1vmax;
    transition: all 0.5s;
}

.offersSection   img:hover{
    box-shadow: 6px 6px 15px rgba(53, 53, 53, 0.363);
  }
.heading{
    display: flex;
    align-items: center;
    justify-content: center;

}


.heading div{
    background-color: rgba(39, 39, 39, 0.274);
    height: 1px;
    width: 30vw;
}

.heading p{
    margin: 3vmax;
    color: rgb(77, 77, 77);
    font-size: 2.5vmax;
    font-family:   Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 100;
}
.starProduct{
    display: flex;
   justify-content: center;
   align-items: center;
   margin: 1vmax auto;
}

.starProduct>div:last-child{
    display: flex;
    flex-wrap: wrap;
    width: 46%;
}

.starProduct img{
    transition: all 0.5s;
}

.starProduct img:hover{
    box-shadow: 6px 6px 15px rgba(53, 53, 53, 0.363);
}

.HotAccessoriesMenu{
    display: flex;
    justify-content: center;
}

.HotAccessoriesLink{
    text-decoration: none;
    color: rgb(94, 94, 94);
    margin: 1vmax;
    padding: 0.5vmax 0;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}

.HotAccessoriesLink:hover{
    text-decoration: none;
    color: orangered;
    border-bottom: 1px solid orangered;
} 

.HotItemCard{
    display: flex;
    flex-direction: column;
    align-items: center; 
    background-color: white;
    width: 31%;
    margin:  0.7vmax;
    transition: all 0.5s;
    cursor: pointer;

}

.HotItemCard:hover{
    -webkit-transform: translateY(-0.4vmax);
            transform: translateY(-0.4vmax);
    box-shadow: 0px 6px 15px rgba(53, 53, 53, 0.363);

}


.HotItemCard > img{
    width: 70%;

}


.HotItemCard > p {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: rgb(85, 83, 83);

}

.HotItemCard > span{
    margin: -1vmax 0 1vmax 0;
    color: orangered;

    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}
.HotAccessories{
    display: flex;
    width: 90vw;
    margin: auto;
}

.HotAccessories > div:first-child > img{
    cursor: pointer;
    transition: all 0.5s;
    width: 22vw;
  }

  .HotAccessories > div:first-child > img:hover{
    -webkit-transform: translateY(-0.4vmax);
            transform: translateY(-0.4vmax);
    box-shadow: 0px 6px 15px rgba(53, 53, 53, 0.363);

}

.HotAccessories > div:last-child{
    display: flex;
    flex-wrap: wrap;
  }
  

.ProductReviewCard{
    margin: 0.5vmax;
    background-color: white;
    padding-bottom: 2vmax;
    cursor: pointer;
    transition: all 0.5s;
}
.ProductReviewCard:hover{
    -webkit-transform: translateY(-0.4vmax);
            transform: translateY(-0.4vmax);
    box-shadow: 0px 6px 15px rgba(53, 53, 53, 0.363);

}

.ProductReviewCard h5{
    margin: 2vmax 0 0.5vmax 2vmax;
    color: rgb(94, 94, 94);
}

.ProductReviewCard span{
    margin: 0 2vmax;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1vmax;
    color: rgb(94, 94, 94);
}

.ProductReviewCard b{
    font-size: 1vmax;
    color: orangered;
}

.ProductReviews{
    display: flex;
    width: 90vw;
    margin: auto;
    justify-content: center;
}


.videoCard{
    width: 30vmax;
    height:48vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-size:cover;
    margin: 0.5vmax;
    cursor: pointer;
    
    }
    
    .videoCard > a {
    padding: 2.5vmax;
    transition: all 0.5s;
    border: none;
    border-radius: 100%;
    background-color: rgba(255, 255, 255, 0.096);
    color: white;
    }
    .videoCard > a > svg {
    fill: rgb(255, 255, 255);
    }
    .videoCard:hover a {
    background-color: rgba(255, 255, 255, 0.671);
    }
    
    .videoCard p {
        color: white;
        margin: 1vmax;
    }
.videos{
    display: flex;
    width: 90vw;
    margin: auto;
}

.PreFooter{
    margin: 5vmax 0 0 0;
    background-color: white;
    padding: 1vmax;
    padding-bottom: 0;
    display: grid;
    grid-template-columns: repeat(3,1fr);
}

.PreFooter > div{
    align-items: center;
    justify-content: center;
    display: flex;
    color:rgba(66, 66, 66, 0.829);
cursor: pointer;
}
.PreFooter > div:hover svg{
fill: rgb(34, 34, 34);
}
.PreFooter > div:hover p{
    color: rgb(19, 18, 18);
}


.PreFooter svg{
   -webkit-transform: scale(2) translateY(-0.2vmax);
           transform: scale(2) translateY(-0.2vmax);
   fill: rgba(37, 37, 37, 0.322);
  margin: 2vmax;

}


/* --------------------PRE FOOTER 2---------- */


.PreFooter2{
    display: grid;
    height: 150px;
    grid-template-columns: repeat(4,1fr); 
  }
  
  .PreFooter2 > div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .PreFooter2 > div > p{
    color: rgba(0, 0, 0, 0.842);
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size:1.5vmax;
    width: 18vmax;
    margin: 0% ;
  }
  .PreFooter2 > div > span{
    color: rgba(51, 51, 51, 0.842);
    width: 18vmax;
    font-size:1vmax;
  }
  
  .PreFooter2 > div > div > input{
    border: none;
    outline: none;
    padding: 0.5vmax 1vmax;
    background-color: rgb(255, 255, 255);
    font-size: 1vmax;
    
    width: 20.4vmax;
  }
  .PreFooter2 > div > div > button{
    border: none;
    padding: 0.5vmax;
    background-color: rgb(255, 255, 255);
    outline: none;
  }
  .PreFooter2 > div > div{
  border: 1px solid rgba(126, 126, 126, 0.493);
  }
  
  .PreFooter2 > div:nth-child(2) > span{
    width: 22vmax;
  }
  
  
  .PreFooter2 > div:nth-child(3){
    justify-content: center;
    align-items:flex-end;
  }
  
  .PreFooter2 > div:nth-child(3) > p{
    width: 14vmax;
  }
  
  .PreFooter2 > div:nth-child(3) > span{
    width: 14vmax;
  }
  .PreFooter2 > div:nth-child(4) {
    flex-direction: row;
  }
  .PreFooter2 > div:nth-child(4) > svg{
    cursor: pointer;
    margin: 1vmax;
    fill: rgba(138, 138, 138, 0.705);
  }
  .PreFooter2 > div:nth-child(4) > svg:hover{
    fill: rgb(94, 94, 94);
  }
  


.footer{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 2fr;
    padding: 4.5vmax;
    background-color: rgb(37, 37, 37);
    color: rgb(197, 197, 197);
}

.footer > div{
    margin:1vmax;
    display: flex;
    flex-direction: column;

}

.footer > div:nth-child(2){
    display: block;
}
.footer > div:nth-child(2) >a{
        display: inline-block;

}
.footer > div > a{
    color: rgb(197, 197, 197);
    font-size: 1vmax;
    text-decoration: none;
    margin: 0.6vmax 0;
}

.footer > div > a:hover{
    color: white;
}


.footer > div:last-child >div{
    width: 45%;
    margin: 1vmax 0;
    font-size: 0.8vmax;
}

.footer > div:last-child >button{
width: 50%;
border:1px solid white;
background-color: rgb(37, 37, 37);
color: white;
padding: 0.6vmax 0;
font-size: 0.98vmax;
font-weight: 100;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
transition: all 0.5s;
}

.footer > div:last-child >button:hover{
    background-color: rgb(248, 244, 244);
    color: rgb(37, 37, 37);
}

.footerBorder{
    background-color:   rgb(37, 37, 37)  ; 
    color: white;
    
}
.footerBorder >div{
    border-top: 1px solid rgba(197, 197, 197, 0.185) ;
    color: rgb(197, 197, 197);
    width: 90vmax;
    margin: auto;
    padding: 2vmax;
   font-size: 0.98vmax;
}


.NavCard{
    width: 10vmax;
    text-align: center;
    cursor: pointer;
}
.NavCard > img{
    width: 10vmax;
}
.NavCard > p{
    margin: 0;
}

.NavCard > span{
    color: orangered;
}
.navOptions{
    display: flex;
    background-color: white;
    justify-content:space-evenly;
    padding: 2vmax;
    z-index: 1000;
}


